@import "../core/variables";
@import "../index";

.results-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 480px;
  height: 85vh;
  border: 1px solid gainsboro;
  border-radius: 5px;
  position: relative;
  left: 80px;
  padding: 40px 40px 25px 40px;
  box-shadow: 5px 0 5px -4px $brown-grey, -5px 0 5px -4px $brown-grey;
  background-color: $white;
  z-index: 1;
}
