@import "../core/variables";
@import "../index";

.header {
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;

  &__logo {
    margin-left: 40px;
    margin-top: 34px;
  }
  &__nav {
    list-style: none;
    display: flex;
    flex-flow: row;
    justify-content: center;
    font-size: 20px;
    font-family: $font;
    font-weight: 500;
    letter-spacing: 0.25px;
    color: $watermelon;
    padding: 37px 29px;
    cursor: pointer;
  }
  &__element {
    padding: 0px 25px;
  }
}
